.navbar {
    background-color: rgb(19, 27, 66);
    border-bottom: 5px solid rgb(250,204,13);
}

.navbar-brand, .navbar-brand:focus {
    color: rgb(250,204,13);
}

.navbar-brand:visited {
    text-decoration: none;
    color: rgb(250,204,13);
}

.navbar-toggler {
    border-color: rgb(250, 204, 13)
}

.navbar-toggler:focus {
    outline: none;
}

.fa-bars {
    color: rgb(250,204,13);
}

.fa-bars:focus, .fas:focus {
    border: none;
}

.navbar-brand:hover {
    color: white;
}

.nav-link {
    color: white;
}

.nav-link:hover, .nav-link:focus {
    color: rgb(250,204,13);
}